import { useEffect, useState } from "react";
import styled from "styled-components";
import Countdown from "react-countdown";
import { Button, CircularProgress, Snackbar } from "@material-ui/core";
import Alert from "@material-ui/lab/Alert";

import './home.css'


import img_hero from "./img/bgm.png";
import img_footer from "./img/lineup.png";
import img_discord from "./img/discord.png";
import img_twitter from "./img/twitter.png";
import img_overlay_s from "./img/overlay-s.png";
import img_overlay_m from "./img/overlay-m.png";

import * as anchor from "@project-serum/anchor";

import { LAMPORTS_PER_SOL } from "@solana/web3.js";

import { useAnchorWallet } from "@solana/wallet-adapter-react";
import { WalletDialogButton } from "@solana/wallet-adapter-material-ui";

import {
  CandyMachine,
  awaitTransactionSignatureConfirmation,
  getCandyMachineState,
  mintOneToken,
  shortenAddress,
} from "./candy-machine";

const ConnectButton = styled(WalletDialogButton)``;

const CounterText = styled.span``; // add your styles here

const MintContainer = styled.div``; // add your styles here

const MintButton = styled(Button)``; // add your styles here

export interface HomeProps {
  candyMachineId: anchor.web3.PublicKey;
  config: anchor.web3.PublicKey;
  connection: anchor.web3.Connection;
  startDate: number;
  treasury: anchor.web3.PublicKey;
  txTimeout: number;
}

const Home = (props: HomeProps) => {
  const [balance, setBalance] = useState<number>();
  const [isActive, setIsActive] = useState(false); // true when countdown completes
  const [isSoldOut, setIsSoldOut] = useState(false); // true when items remaining is zero
  const [isMinting, setIsMinting] = useState(false); // true when user got to press MINT

  const [itemsAvailable, setItemsAvailable] = useState(0);
  const [itemsRedeemed, setItemsRedeemed] = useState(0);
  const [itemsRemaining, setItemsRemaining] = useState(0);

  const [alertState, setAlertState] = useState<AlertState>({
    open: false,
    message: "",
    severity: undefined,
  });

  const [startDate, setStartDate] = useState(new Date(props.startDate));

  const wallet = useAnchorWallet();
  const [candyMachine, setCandyMachine] = useState<CandyMachine>();

  const refreshCandyMachineState = () => {
    (async () => {
      if (!wallet) return;

      const {
        candyMachine,
        goLiveDate,
        itemsAvailable,
        itemsRemaining,
        itemsRedeemed,
      } = await getCandyMachineState(
        wallet as anchor.Wallet,
        props.candyMachineId,
        props.connection
      );

      setItemsAvailable(itemsAvailable);
      setItemsRemaining(itemsRemaining);
      setItemsRedeemed(itemsRedeemed);

      setIsSoldOut(itemsRemaining === 0);
      setStartDate(goLiveDate);
      setCandyMachine(candyMachine);
    })();
  };

  const onMint = async () => {
    try {
      setIsMinting(true);
      if (wallet && candyMachine?.program) {
        const mintTxId = await mintOneToken(
          candyMachine,
          props.config,
          wallet.publicKey,
          props.treasury
        );

        const status = await awaitTransactionSignatureConfirmation(
          mintTxId,
          props.txTimeout,
          props.connection,
          "singleGossip",
          false
        );

        if (!status?.err) {
          setAlertState({
            open: true,
            message: "Congratulations! Mint succeeded!",
            severity: "success",
          });
        } else {
          setAlertState({
            open: true,
            message: "Mint failed! Please try again!",
            severity: "error",
          });
        }
      }
    } catch (error: any) {
      // TODO: blech:
      let message = error.msg || "Minting failed! Please try again!";
      if (!error.msg) {
        if (error.message.indexOf("0x138")) {
        } else if (error.message.indexOf("0x137")) {
          message = `SOLD OUT!`;
        } else if (error.message.indexOf("0x135")) {
          message = `Insufficient funds to mint. Please fund your wallet.`;
        }
      } else {
        if (error.code === 311) {
          message = `SOLD OUT!`;
          setIsSoldOut(true);
        } else if (error.code === 312) {
          message = `Minting period hasn't started yet.`;
        }
      }

      setAlertState({
        open: true,
        message,
        severity: "error",
      });
    } finally {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
      setIsMinting(false);
      refreshCandyMachineState();
    }
  };

  useEffect(() => {
    (async () => {
      if (wallet) {
        const balance = await props.connection.getBalance(wallet.publicKey);
        setBalance(balance / LAMPORTS_PER_SOL);
      }
    })();
  }, [wallet, props.connection]);

  useEffect(refreshCandyMachineState, [
    wallet,
    props.candyMachineId,
    props.connection,
  ]);

  const discord_link = (<a className="blocklink" href="https://discord.gg/cpJ3seJM">More info on our discord</a>)

  return (
    <div className="container" style={{backgroundImage: `url(${img_hero})`}}>

        <section className="hero" style={{backgroundImage: `url(${img_overlay_s})`}} >
          <div className="cta">
            <div className="hl">
              The Lambo<br />
              Diamond Hands Club
            </div>

            <div className="mint">

              <div className="nftinfo">
                {wallet && <p>Availability: {itemsRemaining} out of {itemsAvailable} remaining</p>}
              </div>

            <MintContainer>
              {!wallet ? (
                <ConnectButton>Connect Wallet</ConnectButton>
              ) : (
                <MintButton
                  disabled={isSoldOut || isMinting || !isActive}
                  onClick={onMint}
                  variant="contained"
                >
                  {isSoldOut ? (
                    "SOLD OUT"
                  ) : isActive ? (
                    isMinting ? (
                      <CircularProgress />
                    ) : (
                      "MINT Cost: 2 SOL"
                    )
                  ) : (
                    <Countdown
                      date={startDate}
                      onMount={({ completed }) => completed && setIsActive(true)}
                      onComplete={() => setIsActive(true)}
                      renderer={renderCounter}
                    />
                  )}
                </MintButton>
              )}
            </MintContainer>
            </div>

            <div className="cp">
              The Lambo Diamond Hands Club is a collection of 3900 hand drawn pixel Lambos.<br />
              Thats right, we don’t have any autogenerated stuff here. We did like they would at the Lambo Factory in Sant’Agata Bolognese, Italy.
            </div>

            <div className="mi" style={{backgroundImage: `url(${img_overlay_m})`}} ></div>
          </div>
        </section>

        <div className="content">
        <section className="details">
          <div>
            <div className="hl">Roadmap and Details</div>
            <p>
            We have all the up-to-date details on our Discord!<br />
            We suggest heading on over there to get the latest.</p>
            
            <div className="somelinks">
              <a href="https://discord.gg/cpJ3seJM"><div style={{backgroundImage: `url(${img_discord})`}} /></a>
              <a href="https://twitter.com/PixeLambo"><div style={{backgroundImage: `url(${img_twitter})`}} /></a>
            </div>
          </div>
        </section>

        <section className="items">
          
          <div className="item">
            <div className="blk">img</div>
            <div className="ctx"><strong>Marketplace Listing</strong>
              <p>Once we have minted and sent all of your Lambos The Lambo Diamonds Hands Club will be listed on one of the Solana Marketplaces. Stay tuned!
              {discord_link}
              </p>
            </div>
          </div>

          <div className="item">
            <div className="blk">img</div>
            <div className="ctx"><strong>Airdrop Only Exclusives</strong>
              <p>Think ultra custom models (there are plenty of concept Lambos), custom colours and attributes. These will only be reserved for airdops. No other way to get them!
              {discord_link}
              </p>
            </div>
          </div>

          <div className="item">
            <div className="blk">img</div>
            <div className="ctx"><strong>Merch Store</strong>
              <p>Just in time for all your Christmas shopping!
              Go check it out. Giving out discounts to everyone in our discord. Lambo Holders will get even crazier discounts.
              {discord_link}
              </p>
            </div>
          </div>

        </section>

        <section className="footer" style={{backgroundImage:`url(${img_footer})`}}></section>
      </div>


    <div className="wallet">
      {wallet && (
        <p>Wallet {shortenAddress(wallet.publicKey.toBase58() || "")}</p>
      )}

      {wallet && <p>Balance: {(balance || 0).toLocaleString()} SOL</p>}

      {wallet && <p>Total Available: {itemsAvailable}</p>}

      {wallet && <p>Redeemed: {itemsRedeemed}</p>}

      {wallet && <p>Remaining: {itemsRemaining}</p>}

    </div>

      <Snackbar
        open={alertState.open}
        autoHideDuration={6000}
        onClose={() => setAlertState({ ...alertState, open: false })}
      >
        <Alert
          onClose={() => setAlertState({ ...alertState, open: false })}
          severity={alertState.severity}
        >
          {alertState.message}
        </Alert>
      </Snackbar>

    

</div>
  );
};

interface AlertState {
  open: boolean;
  message: string;
  severity: "success" | "info" | "warning" | "error" | undefined;
}

const renderCounter = ({ days, hours, minutes, seconds, completed }: any) => {
  return (
    <CounterText>
      {hours + (days || 0) * 24} hours, {minutes} minutes, {seconds} seconds
    </CounterText>
  );
};

export default Home;
